body {
  background-color: #000080;
  padding: 20px;
  margin: 0;
}
h1, h2, p, ul, li {
  font-family: sans-serif;
}
ul.profile_page li {
  display: inline;
  list-style-type: none;
  margin: 0;
}
ul.profile_page{
  background-color: #111;
  padding: 0;
}
ul.profile_page li a {
  color: #FF;
  font-weight: bold;
  text-decoration: none;
  padding: 20px;
  display: inline-block;
}

ul.English_game li {
  display: inline;
  list-style-type: none;
  margin: 0;
}
ul.English_game {
  background-color: #360;
  padding: 0;
}
ul.English_game li a {
  color: #F9;
  font-weight: bold;
  text-decoration: none;
  padding: 20px;
  display: inline-block;
}


ul.shop li {
  display: inline;
  list-style-type: none;
  margin: 0;
}
ul.shop {
  background-color: #694;
  padding: 0;
}
ul.shop li a {
  color: #F5;
  font-weight: bold;
  text-decoration: none;
  padding: 20px;
  display: inline-block;
}

.content {
  background-color: #FFF;
  padding: 20px;
}
.content h2 {
  padding: 0;
  margin: 0;
}
.content li {
  margin-bottom: 10px;
}