
.Api {
  font-family: sans-serif;


}
body {
  font-size: 1.2rem;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background: #bdc3c7; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    center,
    #2c3e50,
    #bdc3c7
  ); /* Chrome 10-25, Safari 5.1-6 */

}

.button {
  background-color: #4CAF50;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
   -webkit-transition-duration: 0.4s;
           transition-duration: 0.4s;
}

.result {
  margin-top: 20px;
}

.options {
  background-color: #4CAF50; /* Green */
  border: 1px solid green;
  
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  cursor: pointer;
  width: 150px;
 font-size: 1.3rem;

  -webkit-transition: padding 0.3s;

  transition: padding 0.3s;
  font-family: 'Mako', sans-serif;
  vertical-align:middle;
  font-feature-settings:"c2sc";
  font-variant:small-caps;

}





.options:hover{ 
  background-color: #4CA; /* Green */
  border: 1px solid green;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  cursor: pointer;
  width: 150px;
font-size: 1.3rem;
  text-decoration:none;
  border: none;
  -webkit-transition: padding 0.3s;
  transition: padding 0.3s;
  font-family: 'Mako', sans-serif;
  vertical-align:middle;
font-feature-settings:"c2sc";
font-variant:small-caps;
}

.options:focus {
  outline: none;
  box-shadow: 0 0 0 3px lightskyblue;
}

options:active {
  background-color: #333;
  border-color: #333;
  color: #eee;
}


.button {
  display: inline-block;
  border-radius: 4px;
  background-color: #f4511e;
  border: none;
  color: #FFFFFF;
  text-align: center;
  font-size: 28px;
  padding: 20px;
  width: 200px;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  cursor: pointer;
  margin: 5px;
}






.selected {
  background: -webkit-gradient(linear, left top, left bottom, from(center), color-stop(#74ebd5), to(#acb6e5)) !important;
  background: linear-gradient(center, #74ebd5, #acb6e5) !important;
}

